"use client";

import React, { useState } from "react";

export default function Home() {
  const [hue, setHue] = useState(0);
  // Animate background hue
  React.useEffect(() => {
    const interval = setInterval(() => {
      setHue((h) => (h + 2) % 360);
    }, 30);
    return () => clearInterval(interval);
  }, []);

  // Keyframes as objects for inline animation
  const spin = {
    animation: "spin 2s linear infinite"
  };
  const bounce = {
    animation: "bounce 1s infinite alternate"
  };
  const wiggle = {
    animation: "wiggle 0.4s infinite alternate"
  };
  const pulse = {
    animation: "pulse 1.2s infinite"
  };

  // Inline keyframes
  const keyframes = `
    @keyframes spin { 100% { transform: rotate(360deg); } }
    @keyframes bounce { 0% { transform: translateY(0); } 100% { transform: translateY(-30px); } }
    @keyframes wiggle { 0% { transform: rotate(-8deg); } 100% { transform: rotate(8deg); } }
    @keyframes pulse { 0% { opacity: 1; } 50% { opacity: 0.5; } 100% { opacity: 1; } }
    @keyframes bgmove { 0% { background-position: 0% 50%; } 100% { background-position: 100% 50%; } }
  `;

  return (
    <div
      style={{
        minHeight: "100vh",
        minWidth: "100vw",
        overflow: "hidden",
        position: "relative",
        background: `linear-gradient(120deg, hsl(${hue},100%,70%), hsl(${(hue+120)%360},100%,70%), hsl(${(hue+240)%360},100%,70%))`,
        backgroundSize: "200% 200%",
        animation: "bgmove 10s linear infinite alternate"
      }}
    >
      <style>{keyframes}</style>
      {/* Floating, spinning, bouncing shapes */}
      <div style={{...spin, position: "absolute", top: 40, left: 60, width: 80, height: 80, borderRadius: "50%", background: "rgba(255,255,255,0.3)", border: "4px dashed #fff", zIndex: 1}} />
      <div style={{...bounce, position: "absolute", top: 120, right: 100, width: 60, height: 60, borderRadius: "50%", background: "rgba(0,0,0,0.2)", border: "4px solid #fff", zIndex: 1}} />
      <div style={{...spin, ...pulse, position: "absolute", bottom: 80, left: 120, width: 100, height: 100, borderRadius: "50%", background: "rgba(255,0,150,0.2)", border: "4px dotted #fff", zIndex: 1}} />
      <div style={{...wiggle, position: "absolute", bottom: 40, right: 60, width: 70, height: 70, borderRadius: "50%", background: "rgba(0,255,255,0.2)", border: "4px solid #fff", zIndex: 1}} />
      {/* Main content */}
      <div style={{position: "relative", zIndex: 2, display: "flex", flexDirection: "column", alignItems: "center", justifyContent: "center", minHeight: "100vh"}}>
        <h1 style={{
          fontSize: 64,
          fontWeight: "bold",
          color: `hsl(${(hue+180)%360},100%,30%)`,
          textShadow: "0 4px 32px #fff, 0 2px 8px #0008",
          marginBottom: 24,
          ...wiggle
        }}>
          Welcome to <span style={{color: `hsl(${hue},100%,50%)`, textShadow: "0 0 16px #fff"}}>CRAZY</span> Frontend!
        </h1>
        <p style={{
          fontSize: 28,
          color: `hsl(${(hue+90)%360},100%,20%)`,
          marginBottom: 40,
          ...pulse
        }}>
          This site is wild, animated, and full of energy!
        </p>
        <div style={{display: "flex", gap: 32}}>
          <button style={{
            padding: "20px 40px",
            fontSize: 24,
            fontWeight: "bold",
            borderRadius: 16,
            border: "none",
            background: `linear-gradient(90deg, hsl(${hue},100%,60%), hsl(${(hue+120)%360},100%,60%))`,
            color: "#fff",
            cursor: "pointer",
            boxShadow: "0 8px 32px #0006",
            ...wiggle
          }}
          onMouseEnter={e => e.currentTarget.style.background = `linear-gradient(90deg, hsl(${(hue+60)%360},100%,60%), hsl(${(hue+180)%360},100%,60%))`}
          onMouseLeave={e => e.currentTarget.style.background = `linear-gradient(90deg, hsl(${hue},100%,60%), hsl(${(hue+120)%360},100%,60%))`}
          >
            WIGGLE!
          </button>
          <button style={{
            padding: "20px 40px",
            fontSize: 24,
            fontWeight: "bold",
            borderRadius: 16,
            border: "none",
            background: `linear-gradient(90deg, hsl(${(hue+180)%360},100%,60%), hsl(${(hue+300)%360},100%,60%))`,
            color: "#fff",
            cursor: "pointer",
            boxShadow: "0 8px 32px #0006",
            ...pulse
          }}
          onMouseEnter={e => e.currentTarget.style.background = `linear-gradient(90deg, hsl(${(hue+240)%360},100%,60%), hsl(${(hue+60)%360},100%,60%))`}
          onMouseLeave={e => e.currentTarget.style.background = `linear-gradient(90deg, hsl(${(hue+180)%360},100%,60%), hsl(${(hue+300)%360},100%,60%))`}
          >
            PULSE!
          </button>
        </div>
      </div>
    </div>
  );
} 